html, body {
  background-color: #f0f0f0;
  grid-template: "navbar navbar navbar"
                 "sidebar main sidebar-right"
                 "sidebar section sidebar-right" 25%
                 / auto auto auto;
  width: 100vw;
  height: 100vh;
  margin: 1px;
  padding: 1px;
  font-family: Segoe UI, Verdana, sans-serif;
  font-size: small;
  display: grid;
  overflow: hidden;
}

nav {
  background: #f9f9f9;
  border-radius: 5px;
  grid-area: navbar;
  align-items: center;
  padding: 0 10px;
  display: flex;
}

nav img {
  height: 50px;
  margin: 5px 0;
  display: block;
}

aside.sidebar {
  z-index: 1;
  background: #f9f9f9;
  border-right: 1px solid #ddd;
  grid-area: sidebar;
  width: auto;
  padding: 20px;
  overflow-y: auto;
}

aside.sidebar-right {
  float: right;
  z-index: 1;
  background: #f9f9f9;
  grid-area: sidebar-right;
  width: auto;
  padding: 20px;
  overflow-y: auto;
}

main {
  z-index: 0;
  background: #f9f9f9;
  border: none;
  border-radius: 0;
  grid-area: main;
  margin: 0;
  padding: 0;
  display: flex;
  overflow: hidden;
}

#canvas-container {
  z-index: 0;
  background: #f9f9f9;
  flex: 1;
  width: 100%;
  height: 100%;
  position: relative;
}

canvas {
  z-index: 0;
  width: 100%;
  height: 100%;
  display: block;
}

section {
  background: #f9f9f9;
  grid-area: section;
  padding: 10px;
  overflow: hidden scroll;
}

h1 {
  margin-top: 8px;
  margin-bottom: 8px;
  font-size: large;
  font-weight: 640;
}

h2 {
  margin-top: 8px;
  margin-bottom: 8px;
  font-size: medium;
  font-weight: 640;
}

h3 {
  margin-top: 4px;
  margin-bottom: 4px;
  font-size: small;
  font-weight: 380;
}

.options-container {
  background: #f5f5f5;
  border: 1px solid #ddd;
  border-radius: 8px;
  margin-bottom: 12px;
  padding: 6px;
}

.options-container img {
  width: 250px;
  height: auto;
}

label {
  margin-bottom: 6px;
  font-weight: 600;
  display: block;
}

.slider-group {
  align-items: center;
  gap: 10px;
  margin-bottom: 10px;
  display: flex;
}

input[type="range"], input[type="number"] {
  border: 1px solid #ccc;
  border-radius: 4px;
}

input[type="range"] {
  flex: 1;
}

input[type="number"] {
  width: 70px;
  padding: 4px;
}

.info-icon {
  cursor: pointer;
  color: #fff;
  text-align: center;
  background: #007bff;
  border-radius: 50%;
  width: 18px;
  height: 18px;
  margin-left: 5px;
  font-size: 12px;
  line-height: 18px;
  display: inline-block;
  position: relative;
}

.tooltip {
  color: #333;
  z-index: 9999;
  background: #fff;
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 120px;
  padding: 8px;
  display: none;
  position: absolute;
  top: -5px;
  left: 110%;
  box-shadow: 0 2px 8px #0003;
}

.info-icon:hover .tooltip {
  display: block;
}

.tooltip img {
  width: auto;
  height: 100px;
}

.Mail {
  text-align: center;
  margin-top: 20px;
}

.Mail button {
  color: #fff;
  cursor: pointer;
  background-color: #079ddf;
  border: none;
  border-radius: 5px;
  width: 100%;
  padding: 15px 30px;
  font-size: 24px;
}

.Mail button:hover {
  transform: scale(1.005);
}

.MailPopup {
  visibility: hidden;
  background-color: #f9f9f9;
  border: 1px solid #ddd;
  border-radius: 10px;
  width: 25%;
  height: auto;
  padding: 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.MailPopup .info {
  background: #f5f5f5;
  border: 1px solid #ddd;
  border-radius: 8px;
  margin-bottom: 12px;
  padding: 6px;
}

.MailPopup.open-popup {
  visibility: visible;
}

.MailPopup .bevestig {
  color: #fff;
  background-color: #0fa0d5;
  width: 49%;
}

.MailPopup .terug {
  color: #fff;
  background-color: #efb13d;
  width: 49%;
}

.MailPopup {
  text-align: left;
}
